
.float-btn{
  position: fixed;
  width:4rem; height:4rem; right:2rem;
  border-radius:50%;
  background: rgba(0,0,0,0.3)
}
.float-btn.cambtn{
   bottom:16rem
}
.float-btn a{
  display: block;
  width:100%; height: 100%;
  background: no-repeat;
  background-size:70%;
  background-position: center center;
}
.float-btn.cambtn a{
  background-image: url('./camara.png')
}





