
.select-page{
  width:100%;
  padding:8rem 0;
}
.footer-wrap{
  position: fixed;
  bottom:0;left:0;
  width: 100%;
  height:4rem;
}
.footer-wrap .footer{
  position: relative;
  width: 100%; max-width:640px;
  height:4rem;
  margin: 0 auto;
  background: white;
}
.footer-wrap .footer:after{
  content: "";
  position:absolute;
  width:100%; height: 1px;
  top:-1px; left:0;
  transform:scaleY(0.5);
  -webkit-transform:scaleY(0.5);
  background: #E0E0E0;
}
.footer-wrap .footer .btn{
  position: relative;
  display: inline-block;
  float: left;
  width:50%; height:4rem; line-height: 4rem;
  text-align:center;
}
.footer-wrap .footer .btn:nth-of-type(2):after{
  content: "";
  position: absolute;
  left: 0; top:0;
  height: 100%; width: 1px;
  transform:scaleX(0.5);
  -webkit-transform:scaleX(0.5);
  background: #E0E0E0;
}




