
.tag-list{
  position: relative;
  width:100%;
  border-bottom:1.5rem solid #F3F5F7;/*blue;*/
  margin-bottom: 0.5rem;
}
.tag-list .express{
    position: absolute;
    display: inline-block;
    right:1rem; bottom:-1rem; line-height: 1.5rem;
    font-size: 1.2rem;
}
.tag-list .title{
  height:2rem; line-height: 2rem;
    font-size: 1.4rem;
  padding-left: 0.5rem;
    border-left:3px solid #B91C36;
}
.tag-list .tag-wrap{
  width:100%; max-width:100%;
  padding-left:0.2rem;

  display:-webkit-box; display: -ms-flexbox; display: flex;

  box-orient:horizontal;-webkit-box-orient:horizontal; box-lines:multiple;-webkit-box-lines:multiple;

  -ms-flex-direction:row;

      flex-direction:row; -moz-flex-direction:row;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap; -webkit-flex-wrap:wrap; -moz-flex-wrap:wrap;

  -ms-flex-pack:left;

      justify-content:left;
  box-pack:left;-webkit-box-pack:left;-moz-box-pack:left;
}
.col{
    display: block;
    box-flex:1; -webkit-box-flex:1;
    -ms-flex-positive:0;
     flex-grow:0;
    -ms-flex-negative:1;
        flex-shrink:1;
    -ms-flex-preferred-size:auto;
        flex-basis:auto;
    text-align: center;
    padding:0.5rem 0.5rem;
}
.col .tag{
  display: inline-block;
  width:100%;

  padding:0.5rem 1.2rem;
  border-radius:6px;
  overflow: hidden;
  color:#545454;
  background: white;
}
.col .tag span{
  display: block;
  width:100%; line-height: 2rem;font-size: 1.2rem;
}
.col .tag.active{
    color:white;
    background: #B91C36;
}



