
.main-list {
  width: 100%;
  padding: 8rem 0;
}
.main-list>.list-wrap {
  width: 90%;
  margin: 3rem auto;
}
.main-list>.list-wrap .list-btn {
  width: 100%;
  height: 4rem;
  line-height: 4rem;
  font-size: 1.6rem;
  text-align: center;
  border-radius: 99px;
  margin-bottom: 3rem;
  border: 1px solid #f00;
}


